function maybeAddTag( tagName: string, selector: string | undefined ) {
	if ( ! selector ) {
		return undefined;
	}

	selector = spaceCleaner( selector );
	// eslint-disable-next-line @wordpress/no-unused-vars-before-return
	const [ last = '', ...parts ] = selector.split( ' ' ).reverse();
	if ( ! /^[^a-zA-Z]/.test( last ) ) {
		return selector;
	}

	tagName = tagName.toLowerCase();
	return [ `${ tagName }${ last }`, ...parts ].reverse().join( ' ' );
}
